/* General form styles */
section h3 {
    font-family: Vazirmatn, sans-serif;
  }
  form label {
    font-size: 1rem;
    font-weight: bold;
  }
  option {
    font-size: 1rem;
    padding: 0.5rem;
    font-weight: bold;
  }
  form input {
    font-size: 1rem;
    padding: 0.5rem;
    font-weight: bold;
  }

  /* Mobile-first layout */
  .container {
    padding: 1rem;
  }
  
  h3 {
    font: 'vazirmatn';
    font-size: 1.2rem;
    text-align: center;
  }
  
  button {
    width: 100%; /* Full width for mobile screens */
    padding: 0.8rem;
    font-size: 1rem;
    font-weight: bold;
  }
  
  /* Larger screens (tablet and up) */
  @media (min-width: 768px) {
    h3 {
      font-size: 1.5rem;
    }
  
    button {
      width: auto; /* Auto width for larger screens */
    }
  }
  

  select.form-control {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: transparent;
    padding-right: 30px;
  }
  
 .input-group-append {
    margin-left: -30px;
    z-index: 1;
  }
  
  .input-group-text {
    cursor: pointer;
    background: transparent;
    border: none;
  }